  list-style-type: none;
}

.pagination li {
  display: inline-block;
  height: 31px;
  text-align: center;
}

.pagination a,
.pagination span {
  display: block;
  width: 31px;
  height: 100%;
  color: #626262;
  font-size: 22px;
  font-weight: 700;
  border-radius: 4px;
  text-decoration: none;
  margin: 0 1px;
  position: relative;
}

.pagination a:not(.prev), .pagination a:not(.next),
.pagination span:not(.prev),
.pagination span:not(.next) {
  display: inline-block;
  font-size: 18px;
  line-height: 31px;
}

@media only screen and (max-width: 550px) {
  .pagination a:not(.prev), .pagination a:not(.next),
