.btn-matrix-bordered,
.btn-matrix-cube {
  position: relative;
  color: #333;
  z-index: 1;
}

.btn-matrix-bordered::before,
.btn-matrix-cube::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  box-sizing: border-box;
  border-radius: 5px;
  transform: matrix(0.98, 0, -0.19, 1, 0, 0);
  transition: all 0.3s ease;
