.btn-matrix-bordered:hover::before, .btn-matrix-bordered:focus::before, .btn-matrix-bordered:active::before,
.btn-matrix-cube:hover::before,
.btn-matrix-cube:focus::before,
.btn-matrix-cube:active::before {
