.menu-toggle {
  display: none;
  width: 50px;
  height: 50px;
  position: relative;
  cursor: pointer;
  margin-left: 15px;
}

@media only screen and (max-width: 991px) {
  .menu-toggle {
    display: block;
  }
}

.menu-toggle::before, .menu-toggle::after {
  position: absolute;
  content: '';
  height: 4px;
  left: 0;
  top: 50%;
  background-color: #959595;
  transform: translateY(-50%);
  transition: all .2s ease-in-out;
}

.menu-toggle::before {
  width: 32px;
  margin-top: -5px;
}

.menu-toggle::after {
  width: 20px;
  margin-top: 5px;
}

.menu-toggle:hover::before, .menu-toggle:hover::after {
  background-color: #36b067;
}

.menu-toggle.on::before, .menu-toggle.on::after {
  margin-top: 0;
  background-color: #36b067;
}

.menu-toggle.on::before {
  transform: translateY(-50%) rotate(45deg);
}

.menu-toggle.on::after {
  width: 32px;
  transform: translateY(-50%) rotate(-45deg);
}

